import React, { useState, useEffect } from "react";
import {Link, useNavigate} from "react-router-dom";
// import { db, auth } from "../../firebase-config";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import "../../Components/assets/main.css";
import axios from "axios";
import { baseUrl } from "../../constants";

export default function CreateUser() {
    const [currentpassword, setCurrentPassword] = useState("");
    const [password, setPassword] = useState(null);
    const [confirmpassword, setConfirmPassword] = useState(null);

    const [currentpasswordvalid, setCurrentPasswordValid] = useState(false);
    const [passvalid, setPassValid] = useState(false);
    const [cconfirmpasswordvalid, setConfirmPasswordValid] = useState(false);

    const [loading, setLoading] = useState(false);
    let navigate = useNavigate();
    const authID = localStorage.getItem("UID");
    const token = localStorage.getItem("token");
    const headers = {
        "Content-Type": "application/json",
        Authorization: `Bearer ${token}`,
    };
    useEffect(async () => {

    }, []);


    const handleCurrentPassword = (e) => {
        setCurrentPassword(e.target.value);
        setCurrentPasswordValid(false);
    };

    const handlePassword = (e) => {
        setPassword(e.target.value);
        setPassValid(false);
    };

    const handleConfirmPassword = (e) => {
        setConfirmPassword(e.target.value);
        if(password === e.target.value) {
            setConfirmPasswordValid(false);
        }
        else {
            setConfirmPasswordValid(true);
        }
    };

    async function changePassword(
        current_password,
        password
    ) {
        try {
            const token = localStorage.getItem("token");
            const headers = {
                "Content-Type": "application/json",
                Authorization: `Bearer ${token}`,
            };

            // setLoading(true);

            const bodyForCheck = {
                password: current_password,
            };

            let responsecheck = await axios.get(`${baseUrl}users/checkUserPassword/${JSON.parse(localStorage.getItem("user")).email}/${current_password}`, bodyForCheck, {
                headers,
            });

            if(responsecheck.data.success == 1) {
                console.log(responsecheck.data.data);
                const body = {
                    id: localStorage.getItem("UID"),
                    password: password,
                };

                let response = await axios.post(`${baseUrl}users/changePassword`, body, {
                    headers,
                });

                setLoading(false);
                if(response.data.message === "Password changed successfully") {
                    navigate("/profile");
                    toast.success(response.data.message);
                }
                else {
                    toast.error(response.data.message);
                }
            }
            else {
                setLoading(false);
                toast.error(responsecheck.data.data);
                console.log(responsecheck.data.data);
            }

        } catch (e) {
            console.log(e);
        }
    }

    async function handleSubmit(e) {
        e.preventDefault();
        await changePassword(
            currentpassword,
            password
        );


    }

    function cancelButton(e) {
        e.preventDefault();
        navigate("/profile");
    }
    return (
        <div
            className="flex justify-center items-center bg-indigo-50"
            style={{ height: "90vh" }}
        >
            <div
                className={"loading d-none"}
                style={{ display: loading === true ? "block" : "none" }}
            >
                <img
                    className="loading-image"
                    src="https://cdn.dribbble.com/users/108390/screenshots/2882839/spinner-loop.gif"
                    alt="AdminLTELogo"
                    height="120"
                    width="120"
                    style={{ borderRadius: "50%" }}
                />
            </div>
            <div className="container max-w-3xl mx-auto bg-white rounded-xl shadow-xl overflow-hidden sm:max-w-xl pt-3 pb-3  flex-1 flex flex-col items-center justify-center">
                <div className="px-2 py-1">
                    <div className="uppercase text-3xl textstylecolor font-semibold">
                        Change Password
                    </div>
                    <hr />
                    <form className="w-full max-w-lg" onSubmit={handleSubmit}>
                        <div className="flex flex-wrap -mx-3 mb-1">
                          <div className="w-full px-3">
                            <div className="flex justify-between">
                              <label className="text-xs font-semibold px-1 uppercase block py-1">
                                Current Password
                              </label>
                              <p
                                className={
                                  "inputcolor text-xs italic ml-1 " +
                                  (currentpasswordvalid ? "visible" : "invisible")
                                }
                              >
                                Current Password required.
                              </p>
                            </div>
                            <input
                              type="password"
                              className={
                                "appearance-none block w-full text-gray-700 rounded py-2 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500 " +
                                (currentpasswordvalid
                                  ? "border bordercolor"
                                  : "border border-gray-200")
                              }
                              placeholder="Enter current password"
                              value={currentpassword}
                              // pattern="^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#$%^&*_=+-]).{6,20}$"
                              onChange={handleCurrentPassword}
                              required={true}
                            />
                          </div>
                        </div>

                        <div className="flex flex-wrap -mx-3 mb-1">
                            <div className="w-full px-3">
                                <div className="flex justify-between">
                                    <label className="text-xs font-semibold px-1 uppercase block py-1">
                                        New Password
                                    </label>
                                    <p
                                        className={
                                            "inputcolor text-xs italic ml-1 " +
                                            (passvalid ? "visible" : "invisible")
                                        }
                                    >
                                        New Password required.
                                    </p>
                                </div>
                                <input
                                    type="password"
                                    className={
                                        "appearance-none block w-full text-gray-700 rounded py-2 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500 " +
                                        (passvalid
                                            ? "border bordercolor"
                                            : "border border-gray-200")
                                    }
                                    placeholder="Enter new password"
                                    value={password}
                                    // pattern="^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#$%^&*_=+-]).{6,20}$"
                                    onChange={handlePassword}
                                    required={true}
                                />
                            </div>
                        </div>

                        <div className="flex flex-wrap -mx-3 mb-1">
                            <div className="w-full px-3">
                                <div className="flex justify-between">
                                    <label className="text-xs font-semibold px-1 uppercase block py-1">
                                        Confirm Password
                                    </label>
                                    <p
                                        className={
                                            "inputcolor text-xs italic ml-1 " +
                                            (cconfirmpasswordvalid ? "visible" : "invisible")
                                        }
                                    >
                                        Password do not match.
                                    </p>
                                </div>
                                <input
                                    type="password"
                                    className={
                                        "appearance-none block w-full text-gray-700 rounded py-2 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500 " +
                                        (cconfirmpasswordvalid
                                            ? "border bordercolor"
                                            : "border border-gray-200")
                                    }
                                    placeholder="Confirm password"
                                    value={confirmpassword}
                                    // pattern="^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#$%^&*_=+-]).{6,20}$"
                                    onChange={handleConfirmPassword}
                                    required={true}
                                />
                            </div>
                        </div>

                        <div className="flex flex-wrap -mx-3 mt-3">
                            <div className="w-full md:w-1/2 px-3 mb-2 md:mb-0">
                                <button
                                    type="submit"
                                    className="appearance-none block w-full backcustomcolor text-white font-medium hover:bg-green-500 border border-gray-200 rounded py-2 px-4 leading-tight"
                                    disabled={(cconfirmpasswordvalid
                                        ? true
                                        : false)}
                                >
                                    <span className="mr-2 uppercase">Save</span>
                                </button>
                            </div>
                            <div className="w-full md:w-1/2 px-3 mb-2 md:mb-0">
                                <button
                                    className="appearance-none block w-full backcustomcolor text-white font-medium hover:bg-green-500 border border-gray-200 rounded py-2 px-4 leading-tight"
                                    onClick={cancelButton}
                                >
                                    <span className="mr-2 uppercase">Cancel</span>
                                </button>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    );
}
