import React, { useEffect, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import { Link } from "react-router-dom";
import { db, auth } from "../../firebase-config";
import { toast, ToastContainer } from "react-toastify";
import "../assets/Login.css";
import "react-toastify/dist/ReactToastify.css";
import { globalC as context } from "../../context";
import axios from "axios";
import { baseUrl } from "../../constants";
import { userToJson } from "../../models/user_model";
import moment from "moment-timezone";

export default function XeroLogin() {
  const email = useParams().email;
  const loginType = useParams().type;
  const status = useParams().status;
  const token = useParams().token;
  const type = useParams().process_type;
  let navigate = useNavigate();
  let userRole;
  useEffect(async () => {
    // console.log("working");
    if (localStorage.getItem("token") === null) {
      if (
        (loginType != null && token != null && loginType == "xero") ||
        loginType == "quickbooks"
      ) {
        const headers = {
          "Content-Type": "application/json",
        };

        // await axios.get(`${baseUrl}users/getActiveCompany/${localStorage.getItem("UID")}`, {headers})
        //     .then((res) => {
        //         console.log(res.data.data);
        //         // localStorage.setItem("companyID",res.data.data[0].id);
        //     })

        await axios
          .get(`${baseUrl}users/getLoginToken/${email}`, { headers })
          .then(async (res) => {
            console.log(res.data.data[0]);
            if (res.data.data[0].login_token === token) {
              //
                const body = {
                  email: email,
                };
                let response = await loginData(body);

                if (localStorage.getItem("token") === null) {
                  // alert('Wrong credentials');
                  toast.error("Wrong credentials, Please try again.");
                } else {
                  if (userRole === 1) {
                    if(type==="sign_up") {
                      setTimeout(async function () {
                        navigate("/admindashboard");
                      }, 10000);
                    }
                    else {
                      navigate("/admindashboard");
                    }
                  }
                }
              //
            } else {
              console.log("Token is not equal");
              toast.error("Something went wrong, Please try again.");
              navigate("/");
            }
          });
      } else {
        toast.error("Something went wrong, Please try again.");
        navigate("/");
      }
    } else {
      navigate("/admindashboard");
    }
  }, []);

  function addHours(hours, date = new Date()) {
    let ms = hours * 60 * 60 * 1000;
    date.setTime(date.getTime() + ms);
    return date;
  }

  async function loginData(payload) {
    await axios
      .post(`${baseUrl}xero/xero_login`, payload)
      .then((res) => {
        console.log(res.status);
        // alert(res.status);
        if (res.status === 200) {
          localStorage.setItem("token", res.data.token);
          let user = JSON.stringify(res.data.data);
          console.log("LOGIN DATA USER:::", user);
          localStorage.setItem("user", user);
          localStorage.setItem("loginSuccessMsg", "true");
          localStorage.setItem("UID", res.data.data.id);
          localStorage.setItem("UserName", res.data.data.first_name);
          localStorage.setItem("departID", res.data.data.depart_id);
          localStorage.setItem("type", loginType);
          localStorage.setItem("expired_at",  moment(addHours(3)).unix())
          let role = "";
          userRole = res.data.data.role_id;
          if (res.data.data.role_id == 1) {
            role = "Admin";
          } else if (res.data.data.role_id == 2) {
            role = "Accountant";
          } else if (res.data.data.role_id == 3) {
            role = "Department User";
          } else if (res.data.data.role_id == 4) {
            role = "Management User";
          }
          // else if() {
          //
          // }
          localStorage.setItem("Role", role);
          // localStorage.setItem("loginDetail", JSON.stringify(res.data));
        } else {
          console.log("Login Failed");
        }
      })
      .catch((err) => console.log(err));

    const token = localStorage.getItem("token");
    const headers = {
      "Content-Type": "application/json",
      Authorization: `Bearer ${token}`,
    };

    await axios
      .get(
        `${baseUrl}users/getActivateCompany/${localStorage.getItem("UID")}`,
        { headers }
      )
      .then((res) => {
        console.log("getActivateCompany", res.data.data);
        localStorage.setItem("companyID", res.data.data.id);
      });
  }

  return (
    <div
      className="bg-blue-50 flex justify-center items-center px-5 py-5"
      style={{ height: "100vh" }}
    >
      <div
          className={"loading d-none"}
          style={{ display: type === "sign_in" ? "block" : "none" }}
      >
        <img
            className="loading-image"
            // src={"./loading.gif"}
            src="https://cdn.dribbble.com/users/108390/screenshots/2882839/spinner-loop.gif"
            alt="AdminLTELogo"
            height="100"
            width="100"
            style={{ borderRadius: "10px" }}
        />
        {/*<h4 className={"themeusercolor text-center"}>Loading...</h4>*/}
      </div>
      <div style={{ display: type === "sign_up" ? "block" : "none" }} className="container max-w-3xl mx-auto bg-white rounded-xl shadow-xl overflow-hidden sm:max-w-xl pt-3 pb-3  flex-1 flex flex-col items-center justify-center">
        <div className="px-2 py-1">
          <h2
            style={{
              textAlign: "center",
              fontSize: "20px",
              fontWieght: "bold",
            }}
          >
            {status == 0 ? "Welcome to WePull" : "Welcome back!"}
          </h2>
          <h2 style={{ textAlign: "center" }}>Please Wait.</h2>
          <h5 style={{ textAlign: "center" }}>
            We are collecting your information from {loginType}...
          </h5>
          <img
            style={{ margin: "auto" }}
            src={
              // "./loading.gif"
              // "https://cdn.dribbble.com/users/108390/screenshots/2882839/spinner-loop.gif"
              "https://images.squarespace-cdn.com/content/v1/574faff6f8baf35e5da43485/1504710009065-AT3D2U3AU5704B2VD24R/Data-Inspect.gif"
            }
            width={"200px"}
          />
        </div>
      </div>
    </div>
  );
}
