import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { useNavigate } from "react-router-dom";
import { db, auth } from "../../firebase-config";
import { toast, ToastContainer } from "react-toastify";
import "../assets/Login.css";
import "react-toastify/dist/ReactToastify.css";
import { globalC as context } from "../../context";
import axios from "axios";
import { baseUrl } from "../../constants";
import { useParams } from "react-router-dom";
import { userToJson } from "../../models/user_model";
import moment from "moment-timezone";

export default function Login() {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [emailborder, setEmailBorder] = useState(false);
  const [passborder, setPassBorder] = useState(false);
  const [loading, setLoading] = useState(false);
  let navigate = useNavigate();
  let userRole;
  const status = useParams().status;
  const logintype = useParams().type;
  useEffect(async () => {

    if (localStorage.getItem("token")!=null) {
      // console.log("token found")
      setLoading(true);
      setTimeout(async function () {
        setLoading(false);
        if (JSON.parse(localStorage.getItem("user")).role_id === 1) {
          navigate("/admindashboard");
        } else if (JSON.parse(localStorage.getItem("user")).role_id === 2) {
          navigate("/accountantdashboard");
        } else if (JSON.parse(localStorage.getItem("user")).role_id === 3) {
          console.log("redirect")
          navigate("/dashboard");
        } else if (JSON.parse(localStorage.getItem("user")).role_id === 4) {
          navigate("/dashboard");
        } else if (JSON.parse(localStorage.getItem("user")).role_id === 5) {
          // alert("Login with role");
          navigate("/wepull/dashboard");
        }
      }, 5000);
    }


    if (logintype === "error" && status === "xr") {
      toast.error("Email already registered on Xero");
      navigate('/');
    } else if (logintype === "error" && status === "qb") {
      toast.error("Email already registered on Quickbooks");
      navigate('/');
    }
    else if (logintype === "error" && status === "404") {
      toast.error("Not a valid Connect account.");
      navigate('/');
    }
    else if (logintype === "info" && status === "404") {
      toast.error("There are no more companies to add.");
      navigate('/');
    }
    else if(logintype === "error" && status === "company_not_exist") {
      toast.error("Selected company do not exist on WePull, Please sign in with existing company.");
      navigate('/');
    }
    else if(logintype === "error" && status === "no_tenant") {
      toast.error("Please check the status of your company tenants");
      navigate('/');
    }



  },[]);

  const handleEmail = (e) => {
    setEmail(e.target.value);
    setEmailBorder(false);
  };

  const handlePassword = (e) => {
    setPassword(e.target.value);
    setPassBorder(false);
  };

  function addHours(hours, date = new Date()) {
    let ms = hours * 60 * 60 * 1000;
    date.setTime(date.getTime() + ms);
    return date;
  }

  async function loginData(payload) {
    await axios
      .post(`${baseUrl}users/login`, payload)
      .then((res) => {
        //console.log("status", res.status);
        // alert(res.status);
        if (res.status === 200) {
          localStorage.setItem("token", res.data.token);
          res.data.data.password = "";
          let user = JSON.stringify(res.data.data);
          console.log("LOGIN DATA USER:::", user);
          localStorage.setItem("user", user);
          localStorage.setItem("loginSuccessMsg", "true");
          localStorage.setItem("UID", res.data.data.id);
          localStorage.setItem("UserName", res.data.data.first_name);
          localStorage.setItem("departID", res.data.data.depart_id);
          localStorage.setItem("companyID", res.data.data.company_id);
          localStorage.setItem("type", res.data.data.user_type);
          localStorage.setItem("expired_at",  moment(addHours(3)).unix())
          let role = "";
          userRole = res.data.data.role_id;
          if (res.data.data.role_id == 1) {
            role = "Admin";
          } else if (res.data.data.role_id == 2) {
            role = "Accountant";
          } else if (res.data.data.role_id == 3) {
            role = "Department User";
          } else if (res.data.data.role_id == 4) {
            role = "Management User";
          }else if (res.data.data.role_id == 5) {
            role = "Super Admin";
          }
          localStorage.setItem("Role", role);
          // localStorage.setItem("loginDetail", JSON.stringify(res.data));
        } else {
          console.log("Login Failed");
        }
      })
      .catch((err) => console.log(err));
  }
  async function handleSubmit(e) {
    e.preventDefault();
    if (email === "" || password === "") {
      setEmailBorder(true);
      setPassBorder(true);
    }

    const body = {
      email: email,
      password: password,
    };

    console.log(body);
    setLoading(true);

    let response = await loginData(body);

    if (localStorage.getItem("token") === null) {
      toast.error("Wrong credentials, Please try again.");
      setLoading(false);
    } else {
      setTimeout(async function () {
        setLoading(false);
        if (userRole === 1) {
          // alert("Login with role");
          navigate("/admindashboard");
        } else if (userRole === 2) {
          // alert("Login with role");
          navigate("/accountantdashboard");
        } else if (userRole === 3) {
          // alert("Login with role");
          navigate("/dashboard");
        } else if (userRole === 4) {
          // alert("Login with role");
          navigate("/dashboard");
        } else if (userRole === 5) {
          // alert("Login with role");
          navigate("/wepull/dashboard");
        }
        // message.success('Login successfully');
      }, 5000);
    }
  }
  return (
    <>
      <ToastContainer />
      <div
        className={"loading d-none"}
        style={{ display: loading === true ? "block" : "none" }}
      >
        <img
          className="loading-image"
          // src={"./loading.gif"}
          src="https://cdn.dribbble.com/users/108390/screenshots/2882839/spinner-loop.gif"
          alt="AdminLTELogo"
          height="100"
          width="100"
          style={{ borderRadius: "10px" }}
        />
        {/*<h4 className={"themeusercolor text-center"}>Loading...</h4>*/}
      </div>
      <div className="bg-grey-lighter min-h-screen flex flex-col app">
        <div className="container max-w-lg mx-auto flex-1 flex flex-col items-center justify-center px-2">
          <div className="bg-white px-8 py-4 rounded shadow-md text-black w-full">
            <div className="mb-6 flex justify-center">
              <img
                alt="logo"
                className="object-between w-100 h-10"
                src="https://wepull.netlify.app/finalLogo.png"
              />
            </div>
            <hr />
            <label className="mb-6 block text-3xl font-sans" style={{color:"#1a2956"}}>
              Login
            </label>
            <form onSubmit={handleSubmit}>
              <label className="text-xm font-bold text-gray-700 tracking-wide">
                Email:
              </label>
              <input
                type="text"
                className={
                  "w-full text-lg border-b focus:outline-none focus:border-indigo-500 " +
                  (emailborder ? "bordercolor" : "border-gray-300")
                }
                placeholder="mike@gmail.com"
                value={email}
                onChange={handleEmail}
              />
              <p
                className={
                  "inputcolor text-xm italic mt-1 ml-1 " +
                  (emailborder ? "visible" : "invisible")
                }
              >
                please choose email
              </p>
              <div className="flex justify-between items-center">
                <label className="text-xm font-bold text-gray-700 tracking-wide">
                  Password:
                </label>
                <Link
                  className="font-medium hover:text-xl"  style={{color:"#1a2956"}}
                  to="/confirmEmail"
                >
                  Forgot Password?
                </Link>
              </div>
              <input
                type="password"
                className={
                  "w-full text-lg border-b focus:outline-none focus:border-indigo-500 " +
                  (passborder ? "bordercolor" : "border-gray-300")
                }
                placeholder="Password"
                value={password}
                onChange={handlePassword}
              />
              <p
                className={
                  "inputcolor text-xm italic mb-1 mt-1 ml-1 " +
                  (passborder ? "visible" : "invisible")
                }
              >
                please choose password
              </p>
              <button
                type="submit"
                className="mt-3 mb-2 bg-green-500 text-gray-100 p-2 w-full rounded tracking-wide font-semibold font-display focus:outline-none focus:shadow-outline hover:bg-green-600 shadow-lg"
              >
                Log In
              </button>
            </form>
            <div className="text-grey-dark mt-1  flex justify-center">
              Don't have an account? &nbsp;
              <Link
                className="font-medium text-green-500 hover:text-xl"
                to="/signup"
              >
                 Sign up
              </Link>
            </div>
            <hr />
            <div style={{display:"flex",justifyContent:"space-evenly"}}>
              <a
                  href={`${baseUrl}xero/xero_url/sign_in`}
                  style={{
                    color: "#22b9eb",
                    border: "2px solid #22b9eb",
                    borderRadius: "5px",
                    padding: "10px",
                    fontWeight:"600"
                  }}
              >
                Sign in with Xero
              </a>
              <a
                  href={`${baseUrl}quickbook/quickbooks_url/sign_in`}
                  style={{
                    color: "#0579c6",
                    border: "2px solid #0579c6",
                    borderRadius: "5px",
                    padding: "10px",
                    fontWeight:"600"
                  }}
              >
                Sign in with Intuit
              </a>
            </div>
            <br />
          </div>
        </div>
      </div>
    </>
  );
}
