import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import "./assets/main.css";
import Sidebar from "./pages/Sidebar";
import Navbar from "./pages/Navbar";
export default function Layout(props) {
  let navigate = useNavigate();
  const [show, setShow] = useState(false);
  const [loginstatus, setloginstatus] = useState(false);
  const authToken = localStorage.getItem("token");
  const logmsg = localStorage.getItem("loginSuccessMsg");
  const authID = localStorage.getItem("UID");
  // let setloginstatus = false;
  useEffect(() => {
    if(localStorage.getItem("token")!==null) {
      setloginstatus(true);
    }
    else {
      setloginstatus(false);
      navigate('/');
    }

    if (logmsg === "false") {
      localStorage.setItem("loginSuccessMsg", "true");
    }
    // eslint-disable-next-line
  }, []);
  const handleShow = (e) => {
    e.preventDefault();
    setShow(true);
  };
  const handleHide = (e) => {
    e.preventDefault();
    setShow(false);
  };

  if (loginstatus===true) {
    return (
        <div>
          <ToastContainer />
          <Navbar sidebarShow={handleShow} userID={authID} />
          <Sidebar sidebarHide={handleHide} value={show} />
          <div>{props.child}</div>
        </div>
    );
  }
  else {
    return (
        <div>

        </div>
    );
  }

}
