import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import "../../Components/assets/main.css";
import axios from "axios";
import { baseUrl } from "../../constants";
import { toast } from "react-toastify";

export default function Settings() {
  let navigate = useNavigate();
  const [code, setCode] = useState("");
  const [name, setName] = useState("");
  const [description, setDescription] = useState("");
  const [type, setType] = useState("");
  const [company, setCompany] = useState([]);
  const [selectedCompany, setSelectedCompany] = useState("");
  const authID = localStorage.getItem("UID");
  let tokenSet;
  useEffect(async () => {
    const token = localStorage.getItem("token");
    const headers = {
      "Content-Type": "application/json",
      Authorization: `Bearer ${token}`,
    };
    await axios
      .get(`${baseUrl}users/getCompany/${localStorage.getItem("UID")}`, {
        headers,
      })
      .then((res) => {
        console.log(res.data.data);
        let superarr = [];
        for (let i = 0; i < res.data.data.length; i++) {
          const company_name = res.data.data[i].company_name;
          const id = res.data.data[i].id;
          const active_status = res.data.data[i].active_status;
          if (active_status === 1) {
            setSelectedCompany(res.data.data[i].id);
            localStorage.setItem("companyID", id);
          }
          superarr.push({
            id: id,
            name: company_name,
            active_status: active_status,
          });
        }
        setCompany(superarr);
      });

    if (localStorage.getItem("type") === "xero") {
      await axios
        .get(
          `${baseUrl}xero/xero_refresh_token/${
            JSON.parse(localStorage.getItem("user")).email
          }`,
          { headers }
        )
        .then(async (res) => {
          tokenSet = res.data.tokenSet;
        });
    }
  }, []);

  const handleCompany = (e) => {
    const value = e.target.value;
    setSelectedCompany(value);
    console.log(value);
  };

  async function activateCompany(selectedCompany) {
    try {
      const token = localStorage.getItem("token");
      const headers = {
        "Content-Type": "application/json",
        Authorization: `Bearer ${token}`,
      };
      const body = {
        selectedCompany: selectedCompany,
        user_id: localStorage.getItem("UID"),
      };
      console.log(body);

      let response = await axios.post(`${baseUrl}users/activateCompany`, body, {
        headers,
      });
      toast.success(response.data.message);
      localStorage.setItem("companyID", selectedCompany);
      // navigate('/settings');
      // alert(response.data.message);
      // navigate('/');
    } catch (e) {
      console.log(e);
    }
  }

  async function handleSubmit(e) {
    e.preventDefault();

    await activateCompany(selectedCompany);
  }

  function cancelButton(e) {
    e.preventDefault();
    navigate("/admindashboard");
  }
  return (
    <div
      className="flex justify-center items-center bg-indigo-50"
      style={{ height: "90vh" }}
    >
      <div className="container max-w-3xl mx-auto bg-white rounded-xl shadow-xl overflow-hidden sm:max-w-xl pt-3 pb-3  flex-1 flex flex-col items-center justify-center">
        <div className="px-2 py-1">
          <div
            className="uppercase text-3xl textstylecolor font-semibold text-center"
            style={{ width: "400px" }}
          >
            Settings
          </div>
          <hr />
          <form className="w-full max-w-lg" onSubmit={handleSubmit}>
            <div className="flex flex-wrap -mx-12 mt-3">
              <div className="flex justify-between">
                <label className="block tracking-wide text-gray-700 text-xs font-bold py-1 uppercase">
                  Active Company
                </label>
                {/*<p className={"inputcolor text-xs italic ml-1 mt-3 " + (companyvalid ? "visible" : "invisible")}>Company required</p>*/}
              </div>
              <select
                className={
                  "appearance-none block w-full text-gray-700 rounded py-2 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500 border border-gray-200"
                }
                value={selectedCompany}
                onChange={handleCompany}
              >
                <option selected>Select Company</option>
                {company.length > 0 ? (
                  company.map((part, id) => (
                    <option key={id} value={part.id}>
                      {part.name}
                    </option>
                  ))
                ) : (
                  <option>No Company Found</option>
                )}
              </select>
            </div>
            <div className="flex flex-wrap -mx-12 mt-3">
              <div className="w-full md:w-1/2 px-3 mb-2 md:mb-0">
                <button
                  type="submit"
                  className="appearance-none block w-full backcustomcolor text-white font-medium hover:bg-green-500 border border-gray-200 rounded py-2 px-4 leading-tight"
                >
                  <span className="mr-2 uppercase">Activate</span>
                </button>
              </div>
              <div className="w-full md:w-1/2 px-3 mb-2 md:mb-0">
                <button
                  className="appearance-none block w-full backcustomcolor text-white font-medium hover:bg-green-500 border border-gray-200 rounded py-2 px-4 leading-tight"
                  onClick={cancelButton}
                >
                  <span className="mr-2 uppercase">Cancel</span>
                </button>
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
}
