import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import { db } from "../../../firebase-config";
import "../../assets/main.css";
import axios from "axios";
import { baseUrl } from "../../../constants";
import { toast } from "react-toastify";

export default function CreateAccount() {
  let navigate = useNavigate();
  const [code, setCode] = useState("");
  const [name, setName] = useState("");
  const [description, setDescription] = useState("");
  const [type, setType] = useState("");
  const [company, setCompany] = useState([]);
  const [tenant, setTenant] = useState("");
  const authID = localStorage.getItem("UID");
  let tokenSet;
  useEffect(async () => {
    const token = localStorage.getItem("token");
    const headers = {
      "Content-Type": "application/json",
      Authorization: `Bearer ${token}`,
    };
    await axios
      .get(`${baseUrl}users/getCompany/${localStorage.getItem("UID")}`, {
        headers,
      })
      .then((res) => {
        console.log(res.data.data);
        let superarr = [];
        for (let i = 0; i < res.data.data.length; i++) {
          const company_name = res.data.data[i].company_name;
          const tenant_id = res.data.data[i].tenant_id;
          superarr.push({ id: tenant_id, name: company_name });
        }
        setCompany(superarr);
      });

    if (localStorage.getItem("type") === "xero") {
      await axios
        .get(
          `${baseUrl}xero/xero_refresh_token/${
            JSON.parse(localStorage.getItem("user")).email
          }`,
          { headers }
        )
        .then(async (res) => {
          tokenSet = res.data.tokenSet;
        });
    }
  }, []);
  const handleCode = (e) => {
    setCode(e.target.value);
    // setFnameValid(false);
  };

  const handleName = (e) => {
    setName(e.target.value);
    // setLnameValid(false);
  };
  const handleType = (e) => {
    setType(e.target.value);
    // setEmailValid(false);
  };
  const handleDescription = (e) => {
    setDescription(e.target.value);
  };
  const handleCheck = (e) => {
    const value = e.target.value;
    setTenant(value);
    console.log(value);
  };

  async function createAccount(code, name, type, description, tenant, user_id) {
    try {
      const token = localStorage.getItem("token");
      const headers = {
        "Content-Type": "application/json",
        Authorization: `Bearer ${token}`,
      };
      const body = {
        code: code,
        name: name,
        type: type,
        description: description,
        tenant: tenant,
        user_id: user_id,
        email: JSON.parse(localStorage.getItem("user")).email,
        tokenSet: tokenSet,
      };
      console.log(body);

      let response = await axios.post(
        `${baseUrl}xero/createXeroAccount`,
        body,
        { headers }
      );

      toast.success(response.data.message);

      navigate("/accounts");
      // alert(response.data.message);
      // navigate('/');
    } catch (e) {
      console.log(e);
    }
  }

  async function handleSubmit(e) {
    e.preventDefault();
    // if(fname === ''|| lname === ''|| email === ''|| password === ''|| role===''){
    //     if(fname === ''){
    //         setFnameValid(true)
    //     }
    //     if(lname === ''){
    //         setLnameValid(true)
    //     }
    //     if(email === ''){
    //         setEmailValid(true)
    //     }
    //     if(password === ''){
    //         setPassValid(true)
    //     }
    //     if(role === ''){
    //         setRoleValid(true)
    //     }
    //     return
    // }
    await createAccount(code, name, type, description, tenant, authID);
  }

  function cancelButton(e) {
    e.preventDefault();
    navigate("/accounts");
  }
  return (
    <div
      className="flex justify-center items-center bg-indigo-50"
      style={{ height: "90vh" }}
    >
      <div className="container max-w-3xl mx-auto bg-white rounded-xl shadow-xl overflow-hidden sm:max-w-xl pt-3 pb-3  flex-1 flex flex-col items-center justify-center">
        <div className="px-2 py-1">
          <div
            className="uppercase text-3xl textstylecolor font-semibold text-center"
            style={{ width: "400px" }}
          >
            Create Account
          </div>
          <hr />
          <form className="w-full max-w-lg" onSubmit={handleSubmit}>
            <div className="flex flex-wrap -mx-12 mb-1">
              <div className="w-full px-3">
                <div className="flex justify-between">
                  <label className="text-xs font-semibold px-1 uppercase block py-1">
                    Code
                  </label>
                  {/*<p className={"inputcolor text-xs italic ml-1 " + (lnamevalid ? "visible" : "invisible")}>Password required.</p>*/}
                </div>
                <input
                  type="text"
                  className={
                    "appearance-none block w-full text-gray-700 rounded py-2 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500 border border-gray-200"
                  }
                  placeholder="Enter Account Code"
                  value={code}
                  onChange={handleCode}
                />
              </div>
            </div>
            <div className="flex flex-wrap -mx-12 mb-1">
              <div className="w-full px-3">
                <div className="flex justify-between">
                  <label className="text-xs font-semibold px-1 uppercase block py-1">
                    Name
                  </label>
                  {/*<p className={"inputcolor text-xs italic ml-1 " + (lnamevalid ? "visible" : "invisible")}>Password required.</p>*/}
                </div>
                <input
                  type="text"
                  className={
                    "appearance-none block w-full text-gray-700 rounded py-2 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500 border border-gray-200"
                  }
                  placeholder="Enter Account Name"
                  value={name}
                  onChange={handleName}
                />
              </div>
            </div>
            <div className="flex flex-wrap -mx-12 mb-1">
              <div className="w-full px-3">
                <div className="flex justify-between">
                  <label className="text-xs font-semibold px-1 uppercase block py-1">
                    Type
                  </label>
                  {/*<p className={"inputcolor text-xs italic ml-1 " + (lnamevalid ? "visible" : "invisible")}>Password required.</p>*/}
                </div>
                <input
                  type="text"
                  className={
                    "appearance-none block w-full text-gray-700 rounded py-2 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500 border border-gray-200"
                  }
                  placeholder="Enter Account Type"
                  value={type}
                  onChange={handleType}
                />
              </div>
            </div>
            <div className="flex flex-wrap -mx-12">
              <div className="w-full px-3 mb-1">
                <div className="flex justify-between">
                  <label className="block uppercase tracking-wide text-gray-700 text-xs font-bold py-1">
                    Description
                  </label>
                  {/*<p className={"inputcolor text-xs italic ml-1 " + (emailvalid ? "visible" : "invisible")}>Email required.</p>*/}
                </div>
                <input
                  type="text"
                  className={
                    "appearance-none block w-full text-gray-700 rounded py-2 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500 border border-gray-200"
                  }
                  placeholder="Enter Account Description"
                  // pattern="^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?(?:\.[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?)*$"
                  value={description}
                  onChange={handleDescription}
                />
              </div>
            </div>
            <div className="flex flex-wrap -mx-12">
              <div className="w-full px-3 mb-1">
                <div className="flex justify-between">
                  <label className="block uppercase tracking-wide text-gray-700 text-xs font-bold py-1">
                    Company
                  </label>
                </div>
                <li className="flex flex-row items-center h-5 justify-center transform cursor-pointer text-gray-800">
                  <span className="uppercase ml-1 text-xl m-2 font-medium">
                    Available Companies List
                  </span>
                </li>
                <ul className="appearance-none block w-full text-gray-700 rounded py-2 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500 border border-gray-200 mt-2">
                  {company.length > 0 ? (
                    company.map((part, id) => (
                      <div key={id} className="flex justify-between mt-2 mb-2">
                        <label
                          className="form-check-label inline-block text-gray-800"
                          htmlFor="flexCheckDefault"
                        >
                          {part.name}
                        </label>
                        <input
                          type="radio"
                          className="form-checkbox h-4 w-4 text-blue-600"
                          value={part.id}
                          name={"depart_id"}
                          onChange={handleCheck}
                        />
                      </div>
                    ))
                  ) : (
                    <label
                      className="form-check-label inline-block text-gray-800"
                      htmlFor="flexCheckDefault"
                    >
                      No Company Found
                    </label>
                  )}
                </ul>
              </div>
            </div>
            <div className="flex flex-wrap -mx-12 mt-3">
              <div className="w-full md:w-1/2 px-3 mb-2 md:mb-0">
                <button
                  type="submit"
                  className="appearance-none block w-full backcustomcolor text-white font-medium hover:bg-green-500 border border-gray-200 rounded py-2 px-4 leading-tight"
                >
                  <span className="mr-2 uppercase">Add</span>
                </button>
              </div>
              <div className="w-full md:w-1/2 px-3 mb-2 md:mb-0">
                <button
                  className="appearance-none block w-full backcustomcolor text-white font-medium hover:bg-green-500 border border-gray-200 rounded py-2 px-4 leading-tight"
                  onClick={cancelButton}
                >
                  <span className="mr-2 uppercase">Cancel</span>
                </button>
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
}
