const logout = () => {
    localStorage.removeItem("token");
    localStorage.removeItem("user");
    localStorage.removeItem("UID");
    localStorage.removeItem("UserName");
    localStorage.removeItem("Role");
    localStorage.removeItem("loginSuccessMsg");
    localStorage.removeItem("CompanyID");
    localStorage.removeItem("departID");
    localStorage.removeItem("type");
    localStorage.removeItem("companyID");
    localStorage.removeItem("companyName");
    localStorage.removeItem("currencyCode");
    localStorage.removeItem("currencySymbol");
    localStorage.removeItem("activeDepart");
    localStorage.removeItem("depart_accounts");
    localStorage.removeItem("expired_at");
}

export default logout