import React, { useEffect, useState } from "react";
import {Link, useParams} from "react-router-dom";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { db } from "../../firebase-config";
import axios from "axios";
import { baseUrl } from "../../constants";
// import '../Components/assets/main.css'
import Zoom from 'react-medium-image-zoom'
import 'react-medium-image-zoom/dist/styles.css'

export default function xero_attachment_view() {
    const id = useParams().attach_id;
    const token = localStorage.getItem("token");
    const headers = {
        "Content-Type": "application/json",
        Authorization: `Bearer ${token}`,
    };
    const [image, setImage] = useState("");
    useEffect(async () => {
        console.log("the attach_id:", id)
        await axios
            .get(
                `${baseUrl}xero/viewAttachment/${localStorage.getItem(
                    "UID"
                )}/${id}`,
                {headers}
            )
            .then((res) => {
                console.log("att:", res.data);
                if(res.data.status!==500) {
                    const base64String = btoa(String.fromCharCode(...new Uint8Array(res.data.arrayBuffer.data)));
                    console.log("base64",base64String)
                    // let image = new Image();
                    // image.src = ;
                    setImage("data:image/jpg;base64," + base64String);
                    // let w = window.open("");
                    // w.document.write(image.outerHTML)
                }
                else {
                    setImage("Image url expired please fetch expense again.");
                }

            });
    },[]);

    return (
        <div
            className="flex justify-center items-center bg-indigo-50"
            style={{ height: "100vh" }}
        >
            {image==="Image url expired please fetch expense again."
                ?
                <p>{image}</p>:
                <Zoom>
                    <img
                        alt="Loading image"
                        src={image}
                        style={{marginRight:"auto",marginLeft:"auto"}}
                        // height={"500px"}
                        width="50%"
                    />
                </Zoom>
            }

            {/*<img src={image} width={"50%"}/>*/}
        </div>
    );
}
