import React, {useEffect, useState} from "react";
import {Link, useNavigate, useParams} from "react-router-dom";
import "../assets/Login.css";
import { db, auth } from "../../firebase-config";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import axios from "axios";
import { baseUrl } from "../../constants";

export default function SetupAccount() {

    const [fname, setFirst] = useState(null);
    const [lname, setSecond] = useState(null);
    const [phone, setPhone] = useState(null);
    const [password, setPassword] = useState(null);
    const [confirmpassword, setConfirmPassword] = useState(null);

    const [fnamevalid, setFnameValid] = useState(false);
    const [lnamevalid, setLnameValid] = useState(false);
    const [passvalid, setPassValid] = useState(false);
    const [confirmvalid, setConfirmValid] = useState(false);

    const email = useParams().email;
    const token = useParams().token;

    let navigate = useNavigate();

    useEffect(async () => {
        await axios
            .get(`${baseUrl}users/checkSetupAccount/${email}/${token}`)
            .then(async (res) => {
                // console.log(res.data.data);
                if (res.data.data === 1) {
                    // setBody(true);
                } else {
                    // setBody(false);
                    toast.error("Link Expired");
                    alert("Link Expired");
                    navigate("/");
                }
            });
    },[])

    const handlefirstName = (e) => {
        setFirst(e.target.value);
        setFnameValid(false);
    };
    const handlelastName = (e) => {
        setSecond(e.target.value);
        setLnameValid(false);
    };
    const handlePhone = (e) => {
        setPhone(e.target.value);
    };
    const handlePassword = (e) => {
        setPassword(e.target.value);
        setPassValid(false);
    };
    const handleConfirmPassword = (e) => {
        setConfirmPassword(e.target.value);
        if(password === e.target.value) {
            setConfirmValid(false);
        }
        else {
            setConfirmValid(true);
        }
    };
    async function handleSubmit(e) {
        e.preventDefault();
        try {
            const body = {
                email: email,
                first_name: fname,
                last_name: lname,
                contact: phone,
                password: password
            };
            console.log(body);
            let response = await axios.post(`${baseUrl}users/updateSetupAccount`, body);
            toast.success(response.data.message);

            setTimeout(function () {
                navigate('/');
            },2000)
            // // alert(response.data.message);
            // console.log(response);
            // navigate("/user");
            // alert(response.data.message);
            //
        } catch (e) {
            console.log(e);
        }
    }
    return (
        <div className="bg-grey-lighter min-h-screen flex flex-col app">
            <ToastContainer />
            <div className="container max-w-lg mx-auto flex-1 flex flex-col items-center justify-center px-2">
                <div className="bg-white px-8 py-4 rounded shadow-md text-black w-full">
                    <div className="mb-6 flex justify-center">
                        <img
                            alt="logo"
                            className="object-between w-100 h-8"
                            src="https://wepull.netlify.app/finalLogo.png"
                        />
                    </div>

                    <label className="mb-1 block text-lg font-sans font-medium text-center text-black">
                        Account Setup
                    </label>
                    <hr />
                    <form onSubmit={handleSubmit}>
                        <div className="flex flex-wrap -mx-3 mb-1">
                            <div className="w-full md:w-1/2 px-3">
                              <div className="flex justify-between">
                                <label className="block uppercase tracking-wide text-gray-700 text-xs font-bold py-1 ">
                                  First Name
                                </label>
                                <p
                                  className={
                                    "inputcolor text-xs italic ml-1 " +
                                    (fnamevalid ? "visible" : "invisible")
                                  }
                                >
                                  firstname required.
                                </p>
                              </div>
                              <input
                                type="text"
                                className={
                                  "appearance-none block w-full text-gray-700 rounded py-2 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500 " +
                                  (fnamevalid
                                    ? "border bordercolor"
                                    : "border border-gray-200")
                                }
                                placeholder="Jone"
                                value={fname}
                                onChange={handlefirstName}
                                required={true}
                              />
                            </div>
                            <div className="w-full md:w-1/2 px-3">
                              <div className="flex justify-between">
                                <label className="block uppercase tracking-wide text-gray-700 text-xs font-bold py-1">
                                  Last Name
                                </label>
                                <p
                                  className={
                                    "inputcolor text-xs italic ml-1 " +
                                    (lnamevalid ? "visible" : "invisible")
                                  }
                                >
                                  LastName required.
                                </p>
                              </div>
                              <input
                                type="text"
                                className={
                                  "appearance-none block w-full text-gray-700 rounded py-2 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500 " +
                                  (lnamevalid
                                    ? "border bordercolor"
                                    : "border border-gray-200")
                                }
                                placeholder="Doe"
                                value={lname}
                                onChange={handlelastName}
                                required={true}
                              />
                            </div>
                        </div>
                        <div className="flex flex-wrap -mx-3 mb-1">
                          <div className="w-full px-3">
                            <div className="flex justify-between">
                              <label className="text-xs font-semibold px-1 uppercase block py-1">
                                Password
                              </label>
                              <p
                                className={
                                  "inputcolor text-xs italic ml-1 " +
                                  (passvalid ? "visible" : "invisible")
                                }
                              >
                                Password required.
                              </p>
                            </div>
                            <input
                              type="password"
                              className={
                                "appearance-none block w-full text-gray-700 rounded py-2 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500 " +
                                (passvalid
                                  ? "border bordercolor"
                                  : "border border-gray-200")
                              }
                              placeholder="Enter user password"
                              value={password}
                              // pattern="^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#$%^&*_=+-]).{6,20}$"
                              onChange={handlePassword}
                              required={true}
                            />
                          </div>
                        </div>
                        <div className="flex flex-wrap -mx-3 mb-1">
                            <div className="w-full px-3">
                                <div className="flex justify-between">
                                    <label className="text-xs font-semibold px-1 uppercase block py-1">
                                        Confirm Password
                                    </label>
                                    <p
                                        className={
                                            "inputcolor text-xs italic ml-1 " +
                                            (confirmvalid ? "visible" : "invisible")
                                        }
                                    >
                                        Password Do not match.
                                    </p>
                                </div>
                                <input
                                    type="password"
                                    className={
                                        "appearance-none block w-full text-gray-700 rounded py-2 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500 " +
                                        (confirmvalid
                                            ? "border bordercolor"
                                            : "border border-gray-200")
                                    }
                                    placeholder="Confirm password"
                                    // pattern="^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#$%^&*_=+-]).{6,20}$"
                                    onChange={handleConfirmPassword}
                                    required={true}
                                />
                            </div>
                        </div>
                        {/*<div className="flex flex-wrap -mx-3">*/}
                        {/*    <div className="w-full md:w-1/2 px-3 mb-2 md:mb-0">*/}
                        {/*      <div className="flex justify-between">*/}
                        {/*        <label className="block uppercase tracking-wide text-gray-700 text-xs font-bold py-1">*/}
                        {/*          Phone*/}
                        {/*        </label>*/}
                        {/*      </div>*/}
                        {/*      <input*/}
                        {/*        type="text"*/}
                        {/*        className="appearance-none block w-full text-gray-700 rounded py-2 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500 border border-gray-200"*/}
                        {/*        placeholder="+1 234-567-5678"*/}
                        {/*        // pattern="^(?:(?:\+?1\s*(?:[.-]\s*)?)?(?:\(\s*([2-9]1[02-9]|[2-9][02-8]1|[2-9][02-8][02-9])\s*\)|([2-9]1[02-9]|[2-9][02-8]1|[2-9][02-8][02-9]))\s*(?:[.-]\s*)?)?([2-9]1[02-9]|[2-9][02-9]1|[2-9][02-9]{2})\s*(?:[.-]\s*)?([0-9]{4})(?:\s*(?:#|x\.?|ext\.?|extension)\s*(\d+))?$"*/}
                        {/*        value={phone}*/}
                        {/*        onChange={handlePhone}*/}
                        {/*        required={true}*/}
                        {/*        style={{width:"430px"}}*/}
                        {/*      />*/}
                        {/*    </div>*/}
                        {/*</div>*/}
                        <br />
                        <button
                            type="submit"
                            className="mb-2 flex justify-center py-2 px-4 text-md w-full font-medium rounded-md text-white bg-blue-900 hover:bg-blue-800"
                            disabled={(confirmvalid
                                ? true
                                : false)}
                        >
                            Save and continue
                        </button>
                    </form>
                </div>
            </div>
        </div>
    );
}
