import React, { useState, useEffect } from "react";
import {Link, useNavigate} from "react-router-dom";
// import { db, auth } from "../../firebase-config";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import "../../Components/assets/main.css";
import axios from "axios";
import { baseUrl } from "../../constants";

export default function CreateUser() {
    const [fname, setFirst] = useState(JSON.parse(localStorage.getItem("user")).first_name);
    const [lname, setSecond] = useState(JSON.parse(localStorage.getItem("user")).last_name);
    const [email, setEmail] = useState("");
    const [companyname, setCompanyname] = useState("");
    const [phone, setPhone] = useState(JSON.parse(localStorage.getItem("user")).contact);
    const [password, setPassword] = useState(null);
    const [role, setRole] = useState("");
    const [superpartname, setSuperPartname] = useState([]);
    const [fnamevalid, setFnameValid] = useState(false);
    const [lnamevalid, setLnameValid] = useState(false);
    const [emailvalid, setEmailValid] = useState(false);
    const [emailexist, setEmailExist] = useState(false);
    const [passvalid, setPassValid] = useState(false);
    const [rolevalid, setRoleValid] = useState(false);
    const [phonevalid, setPhoneValid] = useState(false);
    const [depart, setDepart] = useState([]);
    const [selectedDepart, setselectedDepart] = useState([]);

    const [loading, setLoading] = useState(false);
    let navigate = useNavigate();
    const authID = localStorage.getItem("UID");
    const token = localStorage.getItem("token");
    const headers = {
        "Content-Type": "application/json",
        Authorization: `Bearer ${token}`,
    };
    useEffect(async () => {
        await axios
            .get(`${baseUrl}users/getDeparts/${localStorage.getItem("companyID")}`, {
                headers,
            })
            .then((res) => {
                console.log("user",res.data.data);
                var superarr = [];
                for (let i = 0; i < res.data.data.length; i++) {
                    const departdata = res.data.data[i].depart_name;
                    const departid = res.data.data[i].id;
                    superarr.push({ id: departid, name: departdata });
                }
                setDepart(superarr);
            });

    }, []);
    const handlefirstName = (e) => {
        setFirst(e.target.value);
        setFnameValid(false);
    };
    const handlelastName = (e) => {
        setSecond(e.target.value);
        setLnameValid(false);
    };
    const handleEmail = async (e) => {
        setEmail(e.target.value);
        if(e.target.value.length>0) {
            console.log(e.target.value);
            await axios
                .get(`${baseUrl}users/checkUser/${e.target.value}`, { headers })
                .then((res) => {
                    //  console.log("email",res.data.data[0].count);
                    if (res.data.data[0].count > 0) {
                        setEmailExist(true);
                    } else {
                        setEmailExist(false);
                    }
                });
        }

        setEmailValid(false);
    };
    const handlePhone = (e) => {
        setPhone(e.target.value);
    };
    const handlePassword = (e) => {
        setPassword(e.target.value);
        setPassValid(false);
    };
    const handleRole = (e) => {
        setRole(e.target.value);
        setRoleValid(false);
        console.log(e.target.value);
    };
    const handleCheck = (e) => {
        // const value = e.target.value
        // let arr = superpartname;
        // arr.push(value);
        // setselectedDepart(e.target.value);
        const { value, checked } = e.target;
        // const { languages } = userinfo;
        console.log(`${value} is ${checked}`);
        // Case 1 : The user checks the box
        if (checked) {
            let arr = selectedDepart;
            arr.push(value);
            setselectedDepart(arr);
        }
        // Case 2  : The user unchecks the box
        else {
            let arr = selectedDepart;
            const index = arr.indexOf(value);
            if (index > -1) {
                arr.splice(index, 1); // 2nd parameter means remove one item only
            }
            setselectedDepart(arr);
        }
        console.log("selected Depart:" + selectedDepart);
    };

    async function createUser(
        fname,
        lname,
        phone,
    ) {
        try {
            const token = localStorage.getItem("token");
            const headers = {
                "Content-Type": "application/json",
                Authorization: `Bearer ${token}`,
            };

            setLoading(true);

            const body = {
                user_id: localStorage.getItem("UID"),
                first_name: fname,
                last_name: lname,
                contact: phone,
            };

            console.log(body);
            let response = await axios.post(`${baseUrl}users/updateUserProfile`, body, {
                headers,
            });

            // alert(response.data.message);
            console.log(response);


            await axios
                .get(`${baseUrl}users/editUser/${localStorage.getItem("UID")}`, { headers })
                .then((res) => {
                    res.data.data[0].password = "";
                    let user = JSON.stringify(res.data.data[0]);
                    console.log("user",user);
                    localStorage.setItem("user", user);
                    // localStorage.setItem("user",user)
                    // setUserId(res.data.data[0].id);
                    // setFirstname(res.data.data[0].first_name);
                    // setLastname(res.data.data[0].last_name);
                    // setPhone(res.data.data[0].contact);
                    // setuserRole(res.data.data[0].role_id);
                    // setuserDepart(res.data.data[0].depart_id);
                    // setRole(res.data.data[0].role_id);
                    // setselectedDepart(res.data.data[0].depart_id.split(","));
                    // console.log("depart",res.data.data[0].depart_id.split(","));
                });


            setLoading(false);
            if(response.data.message === "Profile updated successfully") {
                navigate("/profile");
                toast.success(response.data.message);
            }
            else {
                toast.error(response.data.message);
            }


            // alert(response.data.message);
            // navigate('/');
        } catch (e) {
            console.log(e);
        }
    }

    async function handleSubmit(e) {
        e.preventDefault();
        // if (
        //     fname === "" ||
        //     lname === "" ||
        //     email === "" ||
        //     password === "" ||
        //     role === ""
        // ) {
        //     if (fname === "") {
        //         setFnameValid(true);
        //     }
        //     if (lname === "") {
        //         setLnameValid(true);
        //     }
        //     if (phone === "") {
        //         setPhoneValid(true);
        //     }
        //     return;
        // }



        await createUser(
            fname,
            lname,
            phone,
        );


    }

    function cancelButton(e) {
        e.preventDefault();
        navigate("/profile");
    }
    return (
        <div
            className="flex justify-center items-center bg-indigo-50"
            style={{ height: "90vh" }}
        >
            <div
                className={"loading d-none"}
                style={{ display: loading === true ? "block" : "none" }}
            >
                <img
                    className="loading-image"
                    src="https://cdn.dribbble.com/users/108390/screenshots/2882839/spinner-loop.gif"
                    alt="AdminLTELogo"
                    height="120"
                    width="120"
                    style={{ borderRadius: "50%" }}
                />
            </div>
            <div className="container max-w-3xl mx-auto bg-white rounded-xl shadow-xl overflow-hidden sm:max-w-xl pt-3 pb-3  flex-1 flex flex-col items-center justify-center">
                <div className="px-2 py-1">
                    <Link to={"/profile/change-password"}><span style={{float:"right"}}>Change Password</span></Link>
                    <div className="uppercase text-3xl textstylecolor font-semibold">
                        Edit Profile
                    </div>
                    <hr />
                    <form className="w-full max-w-lg" onSubmit={handleSubmit}>
                        <div className="flex flex-wrap -mx-3 mb-1">
                            <div className="w-full md:w-1/2 px-3">
                              <div className="flex justify-between">
                                <label className="block uppercase tracking-wide text-gray-700 text-xs font-bold py-1 ">
                                  First Name
                                </label>
                                <p
                                  className={
                                    "inputcolor text-xs italic ml-1 " +
                                    (fnamevalid ? "visible" : "invisible")
                                  }
                                >
                                  firstname required.
                                </p>
                              </div>
                              <input
                                type="text"
                                className={
                                  "appearance-none block w-full text-gray-700 rounded py-2 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500 " +
                                  (fnamevalid
                                    ? "border bordercolor"
                                    : "border border-gray-200")
                                }
                                placeholder="Jone"
                                defaultValue={JSON.parse(localStorage.getItem("user")).first_name}
                                onChange={handlefirstName}
                                required={true}
                              />
                            </div>
                            <div className="w-full md:w-1/2 px-3">
                              <div className="flex justify-between">
                                <label className="block uppercase tracking-wide text-gray-700 text-xs font-bold py-1">
                                  Last Name
                                </label>
                                <p
                                  className={
                                    "inputcolor text-xs italic ml-1 " +
                                    (lnamevalid ? "visible" : "invisible")
                                  }
                                >
                                  LastName required.
                                </p>
                              </div>
                              <input
                                type="text"
                                className={
                                  "appearance-none block w-full text-gray-700 rounded py-2 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500 " +
                                  (lnamevalid
                                    ? "border bordercolor"
                                    : "border border-gray-200")
                                }
                                placeholder="Doe"
                                defaultValue={JSON.parse(localStorage.getItem("user")).last_name}
                                onChange={handlelastName}
                                required={true}
                              />
                            </div>
                        </div>
                        <div className="flex flex-wrap -mx-3">
                            <div className="w-full px-3 mb-1">
                                <div className="flex justify-between">
                                    <label className="block uppercase tracking-wide text-gray-700 text-xs font-bold py-1">
                                        Email
                                    </label>
                                    <p
                                        className={
                                            "inputcolor text-xs italic ml-1 " +
                                            (emailvalid ? "visible" : "invisible")
                                        }
                                    >
                                        Email required.
                                    </p>
                                    <p
                                        className={
                                            "inputcolor text-xs italic ml-1 " +
                                            (emailexist ? "visible" : "invisible")
                                        }
                                    >
                                        Email already exist.
                                    </p>
                                </div>
                                <input
                                    type="email"
                                    className={
                                        "appearance-none block w-full text-gray-700 rounded py-2 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500 " +
                                        (emailvalid
                                            ? "border bordercolor"
                                            : "border border-gray-200")
                                    }
                                    id={"email"}
                                    autoComplete={"off"}
                                    placeholder="Enter user email address"
                                    pattern="^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?(?:\.[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?)*$"
                                    value={JSON.parse(localStorage.getItem("user")).email}
                                    style={{width:"455px"}}
                                    readOnly={true}
                                    onChange={handleEmail}
                                    required={true}
                                />
                            </div>
                        </div>
                        {/*<div className="flex flex-wrap -mx-3 mb-1">*/}
                        {/*  <div className="w-full px-3">*/}
                        {/*    <div className="flex justify-between">*/}
                        {/*      <label className="text-xs font-semibold px-1 uppercase block py-1">*/}
                        {/*        Password*/}
                        {/*      </label>*/}
                        {/*      <p*/}
                        {/*        className={*/}
                        {/*          "inputcolor text-xs italic ml-1 " +*/}
                        {/*          (lnamevalid ? "visible" : "invisible")*/}
                        {/*        }*/}
                        {/*      >*/}
                        {/*        Password required.*/}
                        {/*      </p>*/}
                        {/*    </div>*/}
                        {/*    <input*/}
                        {/*      type="password"*/}
                        {/*      className={*/}
                        {/*        "appearance-none block w-full text-gray-700 rounded py-2 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500 " +*/}
                        {/*        (passvalid*/}
                        {/*          ? "border bordercolor"*/}
                        {/*          : "border border-gray-200")*/}
                        {/*      }*/}
                        {/*      placeholder="Enter user password"*/}
                        {/*      value={password}*/}
                        {/*      // pattern="^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#$%^&*_=+-]).{6,20}$"*/}
                        {/*      onChange={handlePassword}*/}
                        {/*      required={true}*/}
                        {/*    />*/}
                        {/*  </div>*/}
                        {/*</div>*/}
                        {/*<div className="flex flex-wrap -mx-3">*/}
                        {/*    <div className="w-full md:w-1/2 px-3 mb-2 md:mb-0">*/}
                        {/*      <div className="flex justify-between">*/}
                        {/*        <label className="block uppercase tracking-wide text-gray-700 text-xs font-bold py-1">*/}
                        {/*          Phone*/}
                        {/*        </label>*/}
                        {/*          <p*/}
                        {/*              className={*/}
                        {/*                  "inputcolor text-xs italic ml-1 " +*/}
                        {/*                  (phonevalid ? "visible" : "invisible")*/}
                        {/*              }*/}
                        {/*          >*/}
                        {/*              Phone Number Required*/}
                        {/*          </p>*/}
                        {/*      </div>*/}
                        {/*      <input*/}
                        {/*        type="text"*/}
                        {/*        className="appearance-none block w-full text-gray-700 rounded py-2 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500 border border-gray-200"*/}
                        {/*        placeholder="+1 234-567-5678"*/}
                        {/*        // pattern="^(?:(?:\+?1\s*(?:[.-]\s*)?)?(?:\(\s*([2-9]1[02-9]|[2-9][02-8]1|[2-9][02-8][02-9])\s*\)|([2-9]1[02-9]|[2-9][02-8]1|[2-9][02-8][02-9]))\s*(?:[.-]\s*)?)?([2-9]1[02-9]|[2-9][02-9]1|[2-9][02-9]{2})\s*(?:[.-]\s*)?([0-9]{4})(?:\s*(?:#|x\.?|ext\.?|extension)\s*(\d+))?$"*/}
                        {/*        defaultValue={JSON.parse(localStorage.getItem("user")).contact}*/}
                        {/*        style={{width:"455px"}}*/}
                        {/*        onChange={handlePhone}*/}
                        {/*        required={true}*/}
                        {/*      />*/}
                        {/*    </div>*/}
                        {/*</div>*/}
                        <div className="flex flex-wrap -mx-3 mt-3">
                            <div className="w-full md:w-1/2 px-3 mb-2 md:mb-0">
                                <button
                                    type="submit"
                                    className="appearance-none block w-full backcustomcolor text-white font-medium hover:bg-green-500 border border-gray-200 rounded py-2 px-4 leading-tight"
                                >
                                    <span className="mr-2 uppercase">Save</span>
                                </button>
                            </div>
                            <div className="w-full md:w-1/2 px-3 mb-2 md:mb-0">
                                <button
                                    className="appearance-none block w-full backcustomcolor text-white font-medium hover:bg-green-500 border border-gray-200 rounded py-2 px-4 leading-tight"
                                    onClick={cancelButton}
                                >
                                    <span className="mr-2 uppercase">Cancel</span>
                                </button>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    );
}
