import React, { useState } from "react";
import { Link } from "react-router-dom";
import "../assets/Login.css";
import { db, auth } from "../../firebase-config";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import axios from "axios";
import { baseUrl } from "../../constants";

export default function Reset() {
  const [email, setEmail] = useState("");
  const [emailborder, setEmailBorder] = useState(false);
  const [emailexistborder, setEmailExistBorder] = useState(false);
  const [emailsent, setEmailSent] = useState(false);
  const handleEmail = (e) => {
    setEmail(e.target.value);
    setEmailBorder(false);
  };
  async function handleSubmit(e) {
    e.preventDefault();
    if (email === "") {
      setEmailBorder(true);
      setEmailExistBorder(false);
      setEmailSent(false);
      return;
    }

    await axios.get(`${baseUrl}users/checkUser/${email}`).then(async (res) => {
      //console.log(res.data.data[0].count);
      if (res.data.data[0].count === 1) {
        setEmailExistBorder(false);
        setEmailSent(true);
        // passwordReset
        const body = {
          email: email,
        };
        //console.log(body);
        let response = await axios.post(`${baseUrl}users/passwordReset`, body);
        toast.success(response.data.message);
      } else {
        setEmailExistBorder(true);
        setEmailSent(false);
        return;
      }
    });

    // db.collection("Users").where("useremail", "==", email )
    // .get()
    // .then(doc => {
    //   if( doc.docs.length === 0 ) {
    //     toast.warn("Email doesn't exist.")
    //    return
    //   } else{
    //     auth.sendPasswordResetEmail(email)
    //     .then(() =>{
    //       toast.info("Password reset email sent!")
    //     }).catch(error =>{
    //       //console.log(error)
    //     })
    //   }
    // })
  }
  return (
    <div className="bg-grey-lighter min-h-screen flex flex-col app">
      <ToastContainer />
      <div className="container max-w-lg mx-auto flex-1 flex flex-col items-center justify-center px-2">
        <div className="bg-white px-8 py-4 rounded shadow-md text-black w-full">
          <div className="mb-6 flex justify-center">
            <img
              alt="logo"
              className="object-between w-100 h-8"
              src="../finalLogo.png"
            />
          </div>
          <hr />
          <label className="mb-1 block text-2xl font-sans text-center text-black">
            Confirm Email
          </label>
          <form onSubmit={handleSubmit}>
            {/*<label className="mb-6 block text-xm text-center pl-3 text-gray-600">*/}
            {/*  Enter your email and we'll send you password reset link.*/}
            {/*</label>*/}
            <input
              type="email"
              className={
                "block w-full p-1 pl-3 rounded inputBorderColor " +
                (emailborder
                  ? "border-2 bordercolor"
                  : "border border-gray-light")
              }
              placeholder="Email address"
              value={email}
              required={true}
              onChange={handleEmail}
            />
            <p
              className={
                "inputcolor text-xm text-center italic mb-1 mt-1 ml-1 " +
                (emailborder ? "d-block" : "d-none")
              }
            >
              please choose email
            </p>
            <p
              className={
                "inputcolor text-xm text-center italic mb-1 mt-1 ml-1 " +
                (emailexistborder ? "d-block" : "d-none")
              }
            >
              email do not exist.
            </p>
            <p
              className={
                "successcolor text-xm text-center italic mb-1 mt-1 ml-1 " +
                (emailsent ? "d-block" : "d-none")
              }
            >
              We've sent password reset link to your email.
            </p>
            <br />
            <button
              type="submit"
              className="mt-3 mb-2 bg-green-500 text-gray-100 p-2 w-full rounded tracking-wide font-semibold font-display focus:outline-none focus:shadow-outline hover:bg-green-600 shadow-lg"
            >
              Reset password
            </button>
          </form>
          <div className="text-grey-dark mt-1  flex justify-center">
            Don't have an account? &nbsp;&nbsp;
            <Link
              className="font-medium text-green-500 hover:text-xl"
              to="/signup"
            >
              Create Account
            </Link>
          </div>
        </div>
      </div>
    </div>
  );
}
