import { BrowserRouter as Router, Route, Link, Routes } from "react-router-dom";
import Login from "./Components/auth/Login";
import Signup from "./Components/auth/Signup";
import Disconnected_Account from "./Components/auth/disconnected_account";
import ConfirmEmail from "./Components/auth/ConfirmEmail";
import Layout from "./Components/Layout";
import AdminDashboard from "./Layout/Admin/AdminDashboard";
import AccountantDashboard from "./Layout/Account/Accountantdashboard";
import UserDashboard from "./Layout/User/UserDashboard";
import Company from "./Layout/Admin/Company";
import Department from "./Layout/Admin/Department";
import Users from "./Layout/Admin/Users";
import Bill from "./Layout/Admin/Bill";
import Apps from "./Layout/Admin/Application";
import Data from "./Layout/Account/Data";
import Profile from "./Layout/User/Profile";
import AdminNotification from "./Layout/Admin/AdminNotification";
import CreateUser from "./Components/pages/Users/CreateUser";
import EditUser from "./Components/pages/Users/EditUser";
import CreateDepartment from "./Components/pages/department/CreatDepartment";
import EditDepartment from "./Components/pages/department/EditDepartment";
import "./App.css";
import Displaydata from "./Components/Displaydata";
import Credential from "./Components/Credential";
import XeroLogin from "./Components/auth/XeroLogin";
import PasswordReset from "./Components/auth/passwordReset";
import Accounts from "./Layout/Admin/Accounts";
import CreateAccount from "./Components/pages/Accounts/createAccount";
import Settings from "./Layout/Admin/Settings";
import Expenses from "./Layout/Admin/Expenses";
import UserExpense from "./Layout/User/UserExpense";
import AdminProfile from "./Layout/User/AdminProfile";
import DepartmentUsers from "./Layout/User/DepartmentUsers";
import SetupAccount from "./Components/auth/setupAccount";
import EditProfile from "./Layout/User/EditProfile";
import ChangePassword from "./Layout/User/ChangePassword";
import Vendors from "./Layout/Admin/Vendors";
import UserVendors from "./Layout/User/Vendors";
import Xero_Attachment_View from "./Layout/Admin/xero_attachment_view";
import SuperAdminDashboard from "./Layout/SuperAdmin/SuperAdminDashboard";

import React from "react";

function App() {
  return (
    <Routes>
        <Route exact path="/wepull/dashboard" element={<Layout child={<SuperAdminDashboard />} />} />
        <Route exact path="xero_attachment_view/:attach_id" element={<Xero_Attachment_View />} />
      <Route exact path="/" element={<Login />} />
      <Route exact path="/login" element={<Login />} />
      <Route exact path="/login/:type/:status" element={<Login />} />
      <Route
        exact
        path="/auth_login/:email/:type/:status/:token/:process_type"
        element={<XeroLogin />}
      /><Route
        exact
        path="/account/disconnected"
        element={<Disconnected_Account />}
      />
      <Route exact path="/signup" element={<Signup />} />
      <Route exact path="/confirmEmail" element={<ConfirmEmail />} />
      <Route
          exact
          path="/setupAccount/:email/:token"
          element={<SetupAccount />}
      />
      <Route
          exact
          path="/passwordReset/:email/:token"
          element={<PasswordReset />}
      />
      <Route exact path="/displaydata" element={<Displaydata />} />
      <Route exact path="/Credential" element={<Credential />} />
      <Route
        exact
        path="/admindashboard"
        element={<Layout child={<AdminDashboard />} />}
      />
      <Route exact path="/company" element={<Layout child={<Company />} />} />
      <Route exact path="/billSub" element={<Layout child={<Bill />} />} />
      <Route
        exact
        path="/notification"
        element={<Layout child={<AdminNotification />} />}
      />
      <Route exact path="/data" element={<Layout child={<Data />} />} />
      <Route exact path="/accounts" element={<Layout child={<Accounts />} />} />
      <Route exact path="/expenses" element={<Layout child={<Expenses />} />} />
      <Route exact path="/suppliers" element={<Layout child={<Vendors />} />} />
        <Route exact path="/user/suppliers" element={<Layout child={<UserVendors />} />} />
        <Route
        exact
        path="/createAccount"
        element={<Layout child={<CreateAccount />} />}
      />
      <Route exact path="/user" element={<Layout child={<Users />} />} />
      <Route exact path="/app" element={<Layout child={<Apps />} />} />
      <Route
        exact
        path="/createuser"
        element={<Layout child={<CreateUser />} />}
      />
      <Route
        exact
        path="/edituser/:id"
        element={<Layout child={<EditUser />} />}
      />
      <Route
        exact
        path="/categories"
        element={<Layout child={<Department />} />}
      />
      <Route
        exact
        path="/create/category"
        element={<Layout child={<CreateDepartment />} />}
      />
      <Route
        exact
        path="/edit/category/:id"
        element={<Layout child={<EditDepartment />} />}
      />
      <Route
        exact
        path="/accountantdashboard"
        element={<Layout child={<AccountantDashboard />} />}
      />
      <Route
        exact
        path="/dashboard"
        element={<Layout child={<UserDashboard />} />}
      />
      <Route exact path="/profile" element={<Layout child={<Profile />} />} />
      <Route exact path="/profile/edit" element={<Layout child={<EditProfile />}/>} />
      <Route exact path="/profile/change-password" element={<Layout child={<ChangePassword />}/>} />
      <Route exact path="/settings" element={<Layout child={<Settings />} />} />
      <Route
        exact
        path="/admin/profile"
        element={<Layout child={<AdminProfile />} />}
      />
      <Route
        exact
        path="/user/expense"
        element={<Layout child={<UserExpense />} />}
      />
      <Route
        exact
        path="/category/users"
        element={<Layout child={<DepartmentUsers />} />}
      />
    </Routes>
  );
}

export default App;
