import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import { db } from "../../../firebase-config";
import "../../assets/main.css";
import axios from "axios";
import { baseUrl } from "../../../constants";
import { toast } from "react-toastify";
export default function CreateDepartment() {
  const [superpartname, setSuperPartname] = useState("");
  const [partname, setPartname] = useState("");
  const [budget, setBudget] = useState("");
  const [partmanage, setPartmanage] = useState("");
  const [description, setDescription] = useState("");
  const [superpartnamevalid, setSuperpartnameValid] = useState(false);
  const [partmanagervalid, setPartmanagerValid] = useState(false);
  const [selectmanager, setSelectManager] = useState([]);
  const [depart, setDepart] = useState([]);
  const [company, setCompany] = useState([]);
  const [selectedCompany, setselectedCompany] = useState("");
  const [companyvalid, setCompanyValid] = useState(false);
  const [accounts, setAccounts] = useState([]);
  const [selectedAccount, setSelectedAccount] = useState("");
  let navigate = useNavigate();
  const authID = localStorage.getItem("UID");
  const token = localStorage.getItem("token");
  const headers = {
    "Content-Type": "application/json",
    Authorization: `Bearer ${token}`,
  };
  useEffect(async () => {
    console.log("working");

    await axios
      .get(`${baseUrl}users/getCompany/${localStorage.getItem("UID")}`, {
        headers,
      })
      .then((res) => {
        console.log(res.data.data);
        let superarr = [];
        for (let i = 0; i < res.data.data.length; i++) {
          const company_name = res.data.data[i].company_name;
          const tenant_id = res.data.data[i].id;
          superarr.push({ id: tenant_id, name: company_name });
        }
        setCompany(superarr);
      });

    await axios
      .get(
        `${baseUrl}users/getCompanyManagementUsers/${localStorage.getItem(
          "companyID"
        )}`,
        { headers }
      )
      .then((res) => {
        let arr = [];
        for (let i = 0; i < res.data.data.length; i++) {
          console.log(
            res.data.data[i].first_name + " " + res.data.data[i].last_name
          );
          arr.push({
            id: res.data.data[i].id,
            name:
              res.data.data[i].first_name + " " + res.data.data[i].last_name,
          });
        }
        setSelectManager(arr);
      });

    await axios
      .get(`${baseUrl}users/getDeparts/${localStorage.getItem("companyID")}`, { headers })
      .then((res) => {
        let arr = [];
        for (let i = 0; i < res.data.data.length; i++) {
          console.log("depart",res.data.data[i]);
          arr.push(res.data.data[i]);
        }
        setDepart(arr);
      });

    await axios
      .get(`${baseUrl}users/getCompanyAccount/` + localStorage.getItem("UID"), {
        headers,
      })
      .then((res) => {
        let superarr = [];
        for (let i = 0; i < res.data.data.length; i++) {
          const account_name = res.data.data[i].name;
          const id = res.data.data[i].id;
          const type = res.data.data[i].type;
          const code = res.data.data[i].code;
          const company_id = res.data.data[i].company_id;
          const accountID = res.data.data[i].accountID;
          superarr.push({
            id: id,
            name: account_name,
            type: type,
            code: code,
            company_id: company_id,
            accountID: accountID,
          });
        }
        setAccounts(superarr);
      });

    console.log("accounts", accounts);
  }, []);

  function onCancel(e) {
    e.preventDefault();
    navigate("/department");
  }
  const handlesuperpart = (e) => {
    setSuperPartname(e.target.value);
    setSuperpartnameValid(false);
  };
  const handlepartName = (e) => {
    setPartname(e.target.value);
  };
  const handleBudget = (e) => {
    setBudget(e.target.value);
  };
  const handlepartmanage = (e) => {
    setPartmanage(e.target.value);
    setPartmanagerValid(false);
  };
  const handleAccount = (e) => {
    setSelectedAccount(e.target.value);
  };
  const handleDescription = (e) => {
    setDescription(e.target.value);
  };

  async function createDepart(
    parent_id,
    depart_name,
    budget,
    manager_id,
    account_id,
    description
  ) {
    try {
      const token = localStorage.getItem("token");
      const headers = {
        "Content-Type": "application/json",
        Authorization: `Bearer ${token}`,
      };
      const body = {
        parent_id: parent_id,
        depart_name: depart_name,
        budget: budget,
        company_id: localStorage.getItem("companyID"),
        manager_id: manager_id,
        created_by: localStorage.getItem("UID"),
        description: description,
        account_id: account_id,
        depart_id: depart.length + 1
      };
      console.log(body);
      let response = await axios.post(`${baseUrl}users/createDepart`, body, {
        headers,
      });
      toast.success(response.data.message);
      // alert(response.data.message);
      // console.log(response);
      navigate("/department");
      // alert(response.data.message);
      // navigate('/');
    } catch (e) {
      console.log(e);
    }
  }

  async function handleSubmit(e) {
    e.preventDefault();
    await createDepart(
      superpartname,
      partname,
      budget,
      partmanage != "" ? partmanage : null,
      selectedAccount,
      description
    );
  }
  return (
    <div
      className="bg-blue-50 flex justify-center items-center px-5 py-5"
      style={{ height: "100vh" }}
    >
      <div className="container max-w-3xl mx-auto bg-white rounded-xl shadow-xl overflow-hidden sm:max-w-xl pt-3 pb-3  flex-1 flex flex-col items-center justify-center">
        <div className="px-2 py-1">
          <div className="uppercase text-5xl textstylecolor font-Medium text-center mb-3 italic">
            Create Category
          </div>
          <hr />
          <form
            className="w-full max-w-lg object-center"
            onSubmit={handleSubmit}
          >
            <div className="flex flex-wrap -mx-3">
              <div className="w-full px-3">
                <div className="flex justify-between">
                  <div className="block tracking-wide text-gray-700 text-xs font-bold py-1 uppercase">
                    Category
                    <p className="inline flex-shrink-0 mr-3 w-5 h-5 mb-1 text-red-500 text-xl">
                      *
                    </p>
                    <svg
                      className="inline flex-shrink-0 mr-3 w-5 h-5 mb-1"
                      fill="currentColor"
                      viewBox="0 0 20 20"
                      xmlns="http://www.w3.org/2000/svg"
                    >
                      <path
                        fillRule="evenodd"
                        d="M18 10a8 8 0 11-16 0 8 8 0 0116 0zm-7-4a1 1 0 11-2 0 1 1 0 012 0zM9 9a1 1 0 000 2v3a1 1 0 001 1h1a1 1 0 100-2v-3a1 1 0 00-1-1H9z"
                        clipRule="evenodd"
                      ></path>
                    </svg>
                    "this list is managed by WePull"
                  </div>
                  <p
                    className={
                      "inputcolor text-xs italic ml-1 mt-3 " +
                      (superpartnamevalid ? "visible" : "invisible")
                    }
                  >
                    Category required
                  </p>
                </div>
                <select
                  className={
                    "appearance-none block w-full text-gray-700 rounded py-2 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500 " +
                    (superpartnamevalid
                      ? "border bordercolor"
                      : "border border-gray-200")
                  }
                  value={superpartname}
                  onChange={handlesuperpart}
                >
                  <option selected>Choose Category</option>
                  {depart.length > 0 ? (
                    depart.map((part, id) => (
                      <option key={id} value={part.depart_id}>
                        {part.depart_name}
                      </option>
                    ))
                  ) : (
                    <option>Select Category</option>
                  )}
                </select>
              </div>
              <div className="w-full px-3">
                <div className="flex justify-between">
                  <label className="block tracking-wide text-gray-700 text-xs font-bold py-1 uppercase">
                    Sub Category
                  </label>
                </div>
                <input
                  type="text"
                  className="appearance-none block w-full text-gray-700 rounded py-2 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500 border border-gray-200"
                  placeholder="IT"
                  value={partname}
                  onChange={handlepartName}
                />
              </div>
              <div className="w-full px-3">
                <div className="flex justify-between">
                  <label className="block tracking-wide text-gray-700 text-xs font-bold py-1 uppercase">
                    Budget
                  </label>
                </div>
                <input
                  type="text"
                  className="appearance-none block w-full text-gray-700 rounded py-2 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500 border border-gray-200"
                  placeholder="ex: 9999"
                  value={budget}
                  onChange={handleBudget}
                />
              </div>
              <div className="w-full px-3">
                <div className="flex justify-between">
                  <label className="block tracking-wide text-gray-700 text-xs font-bold py-1 uppercase">
                    Category Manager
                  </label>
                  <p
                    className={
                      "inputcolor text-xs italic ml-1 mt-3 " +
                      (partmanagervalid ? "visible" : "invisible")
                    }
                  >
                    Category Manager required
                  </p>
                </div>
                <select
                  className={
                    "appearance-none block w-full text-gray-700 rounded py-2 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500 " +
                    (partmanagervalid
                      ? "border bordercolor"
                      : "border border-gray-200")
                  }
                  value={partmanage}
                  onChange={handlepartmanage}
                >
                  <option selected>Select Manager</option>
                  {selectmanager.length > 0 ? (
                    selectmanager.map((part, id) => (
                      <option key={id} value={part.id}>
                        {part.name}
                      </option>
                    ))
                  ) : (
                    <option>No Management User Found</option>
                  )}
                </select>
              </div>
              {/*<div className="w-full px-3">*/}
              {/*  <div className="flex justify-between">*/}
              {/*    <label className="block tracking-wide text-gray-700 text-xs font-bold py-1 uppercase">*/}
              {/*      Company*/}
              {/*    </label>*/}
              {/*    <p className={"inputcolor text-xs italic ml-1 mt-3 " + (companyvalid ? "visible" : "invisible")}>Company required</p>*/}
              {/*  </div>*/}
              {/*  <select*/}
              {/*      className={"appearance-none block w-full text-gray-700 rounded py-2 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500 border border-gray-200"}*/}
              {/*      value={selectedCompany}*/}
              {/*      onChange={handleCompany}*/}
              {/*  >*/}
              {/*    <option selected>Select Company</option>*/}
              {/*    {company.length > 0 ? (*/}
              {/*        company.map((part ,id) => (*/}
              {/*            <option key={id} value={part.id}>{part.name}</option>*/}
              {/*        ))*/}
              {/*    ) : (*/}
              {/*        <option>No Company Found</option>*/}
              {/*    )*/}
              {/*    }*/}
              {/*  </select>*/}
              {/*</div>*/}
              <div className="w-full px-3">
                <div className="flex justify-between">
                  <label className="block tracking-wide text-gray-700 text-xs font-bold py-1 uppercase">
                    Accounts
                  </label>
                  {/*<p className={"inputcolor text-xs italic ml-1 mt-3 " + (companyvalid ? "visible" : "invisible")}>Company required</p>*/}
                </div>
                <select
                  className={
                    "appearance-none block w-full text-gray-700 rounded py-2 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500 border border-gray-200"
                  }
                  // value={company}
                  onChange={handleAccount}
                >
                  <option selected>Select Account</option>
                  {accounts.length > 0 ? (
                    accounts.map((part, id) => (
                      <option key={id} value={part.id}>
                        ({part.code != null ? part.code : part.accountID}){" "}
                        {part.name}{" "}
                      </option>
                    ))
                  ) : (
                    <option>No Account Found</option>
                  )}
                </select>
              </div>
              <div className="w-full px-3">
                <div className="flex justify-between">
                  <label className="block tracking-wide text-gray-700 text-xs font-bold py-1 uppercase">
                    Description
                  </label>
                </div>
                <textarea
                  className="appearance-none block w-full text-gray-700 rounded py-2 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500 border border-gray-200"
                  rows="5"
                  minLength="20"
                  maxLength="1000"
                  size="150"
                  placeholder="Write about category..."
                  value={description}
                  onChange={handleDescription}
                />
              </div>
            </div>
            <div className="flex flex-wrap -mx-3 mt-3">
              <div className="w-full md:w-1/2 px-3 mb-2 md:mb-0">
                <button
                  type="submit"
                  className="appearance-none block w-full backcustomcolor text-white font-medium hover:bg-green-500 border border-gray-200 rounded py-2 px-4 leading-tight"
                >
                  <span className="mr-2 uppercase">Create</span>
                </button>
              </div>
              <div className="w-full md:w-1/2 px-3 mb-2 md:mb-0">
                <button
                  className="appearance-none block w-full backcustomcolor text-white font-medium hover:bg-green-500 border border-gray-200 rounded py-2 px-4 leading-tight"
                  onClick={onCancel}
                >
                  <span className="mr-2 uppercase">Cancel</span>
                </button>
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
}
